import React from "react"
import { connect } from 'react-redux'
import { Result, List, WhiteSpace, Button, Modal } from "antd-mobile"
import browserCookie from 'browser-cookies'
import { logoutSubmit } from "./../../redux/user.redux"
import { Redirect } from 'react-router-dom'

@connect(
  state => state.user,
  { logoutSubmit }
)
class User extends React.Component {
  constructor(props) {
    super(props)

    this.logout = this.logout.bind(this)
  }

  logout() {
    
    const alert = Modal.alert

    alert('', '', [
      { text: '', onPress: () => console.log('cancel') },
      { text: '', onPress: () => {
        browserCookie.erase('userid')
        this.props.logoutSubmit()
      } }
    ])
  }
  render() {
    const props = this.props
    const Item = List.Item
    const Brief = Item.Brief
    return props.user ? (
      <div>
        <Result
          img={<img src={require(`./../img/${props.avatar}.png`)} style={{width:50}} alt="" />}
          title={props.user}
          message={props.type==='boss'?props.company:null}
          ></Result>
        <List renderHeader={()=>''}>
          <Item
            multipleLine
          >
            {props.desc}
            {props.desc.split('\n').map((v, i) => (<Brief key={v + i}>{v}</Brief>))}
            {props.money?<Brief>{props.money}</Brief>:null}
          </Item>
        </List>
        <WhiteSpace></WhiteSpace>
				<Button type="warning" onClick={this.logout}></Button>
      </div>
    ) : <Redirect to={props.redirectTo}></Redirect>;
  }
}

export default User


